export default "<template>\n  <div class=\"MonHocToan\">\n    <raw-loader :mytxt=\"require('!raw-loader!./AxiosUpload.vue').default\"></raw-loader>\n    <h1>AxiosUpload page</h1>\n    <div>\n\n      <b-row>\n        <b-col md=\"2\" class=\"border-right\">\n          <form enctype=\"multipart/form-data\" @submit.stop.prevent=\"onUpload\">\n            <b-img ref=\"output\" fluid alt=\"Responsive image\"></b-img>\n            <input type=\"file\" id=\"file\" ref=\"file\" class=\"m-1\" @change=\"onFileChange\" /><br>\n            <div>\n              <button type=\"submit\" class=\"mr-1\">Upload</button>\n              <button @click=\"onRemove\" class=\"mr-1\">Remove</button>\n            </div>\n          </form>\n        </b-col>\n        <b-col md=\"10\">\n          <b-row>\n            <b-col md=\"1\" v-for=\"x in ds\" :key=\"x.imageName\" class=\"border-right\">\n              <img width=\"100%\" height=\"40\" fluid :src=\"`/api/FileUpload/GetImage/${x.imageName}`\" alt=\"Image 1\" />\n            </b-col>\n          </b-row>\n          <hr>\n        </b-col>\n      </b-row>\n\n    </div>\n  </div>\n</template>\n<script>\nimport $ from \"jquery\";\nimport axios from \"axios\";\nexport default {\n  name: 'Tpl',\n  components: {\n  },\n  data() {\n    return {\n      ds: []\n    }\n  },\n  methods: {\n    onUpload() {\n      let files = this.$refs.file.files;\n      if (files.length == 0) return;\n      let file = files[0];\n      var formData = new FormData();\n      formData.append(\"myfile\", file);\n      formData.append(\"myName\", \"thuc 101\");\n      this.UploadAxios(formData).then(res => {\n        this.ds.push(res.data.rec);\n        $(this.$refs.file).val(\"\");\n        $(this.$refs.output).attr(\"src\", \"\");\n      })\n    },\n    onFileChange() {\n      let files = this.$refs.file.files;\n      let file = files[0];\n      this.getReadAsDataURL(file).then((res) => {\n        $(this.$refs.output).attr(\"src\", res);\n      });\n    },\n    onRemove() {\n      $(this.$refs.output).attr(\"src\", \"\");\n      $(this.$refs.file).val(\"\");\n    },\n    getReadAsDataURL(file) {\n      return new Promise((resolve, reject) => {\n        let reader = new FileReader();\n        reader.onload = () => {\n          resolve(reader.result);\n        };\n        reader.onerror = reject;\n        reader.readAsDataURL(file);\n      });\n    },\n    async UploadAxios(formData) {\n      return await axios\n        .post(`/api/FileUpload/UploadAxios`, formData)\n        .catch((err) => {\n          console.log(err);\n        });\n    },\n  }\n}\n</script>\n";